$h: 10px;
$color: rgb(58, 59, 59);
$width: 200;
$gradient: linear-gradient(to right, rgb(95, 95, 223), rgb(211, 125, 225));
@mixin boxSizing {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

@mixin transform($transforms) {
    -moz-transform: $transforms;
    -o-transform: $transforms;
    -ms-transform: $transforms;
    -webkit-transform: $transforms;
    transform: $transforms;
}

@mixin rotate($deg) {
    @include transform(rotate(#{$deg}deg));
}
@mixin clearfix {
    &:after {
        clear: both;
        content: '.';
        display: block;
        height: 0;
        line-height: 0;
        overflow: hidden;
    }
    *height: 1%;
}
@mixin border($borderWidth:1px,$borderColor:#d9d9d9,$borderStyle:solid) {
    border: $borderWidth $borderColor $borderStyle;
}
.radioInput {
    padding: 10;
    margin: 40;
    background: rgb(251, 251, 250);
    box-shadow: 0 0 10px rgba(0,0,0,0.5); 
    
    border-radius: 0.8rem;
    @include clearfix;
    li{
      list-style: none;
     
      margin: 9px 4px;
      position: absolute;
    z-index: 100;
        @include clearfix;
    em{
        display: inline-block;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        @include border;
        float: left;
      }

    em.selected {
    position: relative;
    border: 1px solid $gradient;;
    background: $gradient;;
    &:before {
        display: block;
        content: '';
        width: 6px;
        height: 2px;
        background: white;
        position: absolute;
        left: 4.5px;
        top: 10.6px;
        @include rotate(45)
    }
    &:after {
        display: block;
        content: '';
        width: 10px;
        height: 2px;
        background: white;
        position: absolute;
        left: 8px;
        top: 8px;
        @include rotate(-45)
    }
    }
    }
}
