.inner {
    input[type=text] {
        font-family: 'Josefin Sans', sans-serif;
        height: $h*4;
        @include border;
        background: #fff;
        width: $width*2+163+px;
        padding: 0 5px;
        border-radius: 0.8rem;
        position: relative;
        padding-left: 31px;
    }
    button {
        height: $h*4+2;
        border: none;
        display:none;
        background: $color;
        color: #fff;
        cursor: pointer;
        margin-left: 9px;
        &:hover {
            color: #fff;
            background: rgba($color, 0.7);
        }
    }
}